@use "../../styles/theme" as theme;

.links-container {
	height: 100vh;
	width: 100vw;
	display: flex;
	align-items: center;
	flex-direction: column;

	background-size: cover;
	background-image: url("../../assets/squares.svg");

	.title {
		margin-top: 4em;
		margin-bottom: 1%;

		p {
			font-size: 5rem;
			margin: 0;
		}

		svg {
			rotate: 180deg;
			filter: invert(100%) brightness(102%);
		}
	}

	.linkedlist {
		max-width: 666px;
		width: 100%;
		height: 100%;

		.link {
			max-height: 55px;
			display: flex;
			flex-direction: row;
			align-items: center;

			margin-top: 1em;
			margin-bottom: 1em;

			border-radius: 30px;

			// BACKUP hover that fills center
			// border: 3px solid theme.$slate;
			// background-color: theme.$slate;

			// &:hover {
			// 	background-position: 0 100%;
			// 	background: theme.$periwinkle;
			// 	transition-duration: 0.6s;
			// }

			background: linear-gradient(
				90deg,
				theme.$blue 0%,
				theme.$blue-munsell 40%
			);

			&:hover {
				border: 4px solid theme.$swamp-lighter;
				transition-duration: 0.3s;
			}

			svg {
				width: auto;
				height: 3.5rem;
				margin-left: 1rem;
			}

			p {
				margin-right: 1.75em; // Match half of svg width

				width: 100%;
				text-align: center;
				font-size: 2.5rem;
				font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
					"Lucida Sans", Arial, sans-serif;
			}
		}
	}
}
